import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

const MARKDOWN_OPTIONS = {
	pedantic: false,
	gfm: true,
	breaks: true,
	sanitize: false,
	smartLists: true,
	smartypants: true,
	xhtml: true,
};

import mu from '../../lib/musher-util';

const initialState = {
	isLoading: false,
};

@observer
class Races extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
            raceIdx: 0,
        };
    }

    async loadRace(props = this.props) {
		this.setState({ isLoading: true });
		const { raceStore } = props.stores;
		const query = {
            relevant: 1,
        };
		await raceStore.load({ query });
		this.setState({ isLoading: false });
	}

    toggleRaces = async () => {
		const { userStore } = this.props.stores;
        const { user } = userStore;
        const { toggleFeature = {} } = user;
        const value = toggleFeature.racesFrontpage ? 0 : 1;
        this.setState({ hideRaces: value });
        await userStore.setToggleFeature({
            field: 'toggleFeature.racesFrontpage',
            value,
        });
    }

    scrollRaces = (e) => {
        // scrollHeight: 705
        // scrollLeft: 564
        // scrollTop: 0
        // scrollWidth: 1692

        const { scrollLeft, scrollWidth, clientWidth } = e.target;
        const step = clientWidth;
        const raceIdx = scrollLeft / step;

        const nearestInt = Math.round(raceIdx);
        const diff = Math.abs(nearestInt - raceIdx);
        // console.log({ e, step, raceIdx, scrollLeft, scrollWidth, clientWidth, nearestInt, diff });
        // console.log({ nearestInt, diff });
        if (Number.isInteger(raceIdx) || diff < 0.5) {
            this.setState({
                raceIdx: nearestInt,
            });
        }
    }

    componentDidMount() {
		this.loadRace();
	}

	// componentWillReceiveProps(nextProps) {
    //     if (this.props.)
	// 	this.loadRace(nextProps);
	// }

    render() {
        const { appState, userStore, raceStore, newsStore } = this.props.stores;
		const { allUsers, isAdmin, user, notifications = [] } = userStore;
		const { races = [] } = raceStore;
		const { newsList } = newsStore;
		const news = newsList && newsList[0] ? newsList[0] : null;
		const { darkmode } = appState;
		const { showNews, hideRaces, raceIdx } = this.state;
        const { toggleFeature = {}, language = 'en' } = user;

        const today = new Date();

        if (!races || races.length === 0) {
            return '';
        }

        return (
            <div class='row'>
                {hideRaces || toggleFeature.racesFrontpage ? <>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-1'>
                        <div class='row'>
                            <div class={`col-12 bg-${darkmode ? 'black' : 'white'} rounded-lg position-relative px-0`}>
                                <div class='w-100 d-flex justify-content-center'>
                                    <button class='btn btn-lg btn-block btn-link' onClick={this.toggleRaces}>
                                        <i class='fas fa-flag-checkered' /> <Text id='races.show-races'>Show Races</Text> {races.length > 1 && <>- {races.length}</>}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </> : <>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-1'>
                        <div class='row'>

                            <div class='w-100 position-relative'>
                                <div
                                    class='d-flex flex-row flex-nowrap bg-light border-top border-bottom'
                                    style='overflow: auto; scroll-snap-type: x mandatory;'
                                    onScroll={this.scrollRaces}
                                >
                                    {races && races.map((race, idx) =>  {
                                        let status = '';
                                        if (today >= new Date(race.startDate) && today <= new Date(race.endDate)) {
                                            status = 'race';
                                        } else if (today >= new Date(race.signupStartDate) && today <= new Date(race.signupEndDate)) {
                                            status = 'signup';
                                        } else if (today > new Date(race.endDate)) {
                                            status = 'done';
                                        }
                                        return (<>
                                            <div
                                                class={`col-${races.length === 1 ? '12' : '8 ml-4'} ${races.length === idx + 1 ? 'mr-5' : ''} mb-0 bg-${darkmode ? 'black' : 'white'} rounded-lg position-relative`}
                                                style={`
                                                    scroll-snap-align: center;
                                                    flex-wrap: wrap;
                                                    overflow-y: hidden;
                                                `}
                                                onTouchstart={(e) => { e.stopPropagation(); }}
                                                onTouchend={(e) => { e.stopPropagation(); }}
                                                onTouchmove={(e) => { e.stopPropagation(); }}
                                            >
                                                <div class='row d-flex align-items-center position-relative'>
                                                    <h5 class='position-absolute' style='top: 3px; right: 5px;'>
                                                        {status === 'signup' && !race.liveRace && <>
                                                            <span class='badge badge-primary border border-white ml-2'><Text id='races.signup-open'>Pmelding pen</Text></span>
                                                        </>}
                                                        {status === 'race' && <>
                                                            <span class='badge badge-danger border border-white ml-2'><Text id='races.race-in-progress'>Lp pgr</Text></span>
                                                        </>}
                                                        {status === 'done' && <>
                                                            <span class='badge badge-success border border-white ml-2'><Text id='races.race-finished'>Lp avsluttet</Text></span>
                                                        </>}
                                                    </h5>
                                                    {(race.images && race.images[0]) && <>
                                                        <div
                                                            class={`w-100`}
                                                            style={`background-image: url("${race.images[0].s3LargeLink}"); background-size: cover; height: 100px; background-repeat: no-repeat; background-position: 50% 50%;`}
                                                            onClick={() => route(`/app-races/${race.id}`)}
                                                        />
                                                    </>}
                                                </div>
                                                <div class='position-relative'>
                                                    <h5 class='position-absolute' style='top: 10px; right: 10px;'>
                                                        {status === 'signup' && <>
                                                            <div class='spinner-grow text-primary' role='status' />
                                                        </>}
                                                        {status === 'race' && <>
                                                            <div class='spinner-grow text-danger' role='status' />
                                                        </>}
                                                        {status === 'done' && <>
                                                            <span><i class='fas fa-check text-success' /></span>
                                                        </>}
                                                    </h5>
                                                    <h5 class='pt-2'>
                                                        {isAdmin && <i class={`fas fa-eye${race.active ? ' text-success' : '-slash text-danger'} float-right`} />}
                                                        <a href={`/app-races/${race.id}`}> 
                                                            {race.title}
                                                        </a>
                                                    </h5>
                                                </div>
                                                <div class='w-100'>
                                                    <Markdown markdown={mu.replaceImages(race.ingress, race.images, language)} markdownOpts={MARKDOWN_OPTIONS} />
                                                </div>
                                                <div class='w-100'>
                                                    {race.classes && <>
                                                        <div class='mb-3'>
                                                            <small class='font-weight-lighter mr-2'><Text id='races.classes'>Klasser</Text>: </small><br />

                                                            <div class='w-100 position-relative'>
                                                                <div
                                                                    class='d-flex flex-row flex-nowrap'
                                                                    style='overflow: auto; scroll-snap-type: x mandatory;'
                                                                >
                                                                    {race.classes.sort((a, b) => a.distance - b.distance).map(currentClass => <>
                                                                        <span class='badge badge-pill badge-light mr-2 font-weight-light'>
                                                                            {currentClass.title}
                                                                            {/* <span class='badge badge-info ml-2 font-weight-light'>{currentClass.contestants.length}</span> */}
                                                                        </span>
                                                                    </>)}
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </>}
                                                </div>
                                                <div class='w-100 d-flex justify-content-between'>
                                                    <a href={`/app-races/${race.id}/tracking`} class='btn btn-primary'>
                                                        <Text id='races.tracking'>Tracking</Text>
                                                        <i class='fas fa-map-marked ml-2' />
                                                    </a>
                                                    <a href={`/app-races/${race.id}`} class='btn btn-primary'>
                                                        <Text id='races.read-more'>Les mer</Text>
                                                        <i class='fas fa-angle-double-right ml-2' />
                                                    </a>
                                                </div>
                                                <div class='w-100 d-flex justify-content-center'>
                                                    <small class='text-muted'>
                                                        <Text id='races.race-days'>Lpsdager</Text>: {race.startDate && <>{util.formatDate(race.startDate, { locale: language })}</>}
                                                        {race.endDate && <> - {util.formatDate(race.endDate, { locale: language })}</>}
                                                    </small>
                                                </div>
                                            </div>
                                        </>);
                                    })}
                                </div>
                            </div>

                            {races && races.length > 1 && <>
                                <div class='w-100 text-center'>
                                    <small>
                                        <small>
                                            {races && races.map((img, idx) => <>
                                                <i class={`${idx === raceIdx ? 'fas' : 'far'} fa-circle mr-1`} />
                                            </>)}
                                        </small>
                                    </small>
                                </div>
                                <div
                                    class='position-absolute text-white font-weight-lighter px-1 py-1 rounded-lg'
                                    style='top: 10px; right: 10px; background-color: rgba(0, 0, 0, 0.3); line-height: 1.0em;'
                                >
                                    <small>{raceIdx + 1} / {races.length}</small>
                                </div>
                            </>}

                            <div class={`col-12 mb-1 bg-${darkmode ? 'black' : 'white'} rounded-lg position-relative`}>
                                <div class='w-100 d-flex justify-content-center'>
                                    <button class='btn btn-link' onClick={this.toggleRaces}>
                                        <i class='fas fa-angle-up' /> <Text id='races.hide'>Hide Races</Text>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </>}
            </div>
        );
    }
}

export default Races;
