import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

@observer
class Invite extends Component {
    inviteKennel = async () => {
        const { friendEmail: email } = this.state;
		const { userStore } = this.props.stores;
        const response = await userStore.inviteKennel({ email });
        this.setState({
            response,
            inviteSent: true,
        });
    }

    inviteNew = () => {
        this.setState({
            inviteSent: false,
        });
    }

    hideInvite = async () => {
        const { userStore } = this.props.stores;
        await userStore.updateSetting({ 'settings.hideInvite': 1 });
        this.setState({
            hideInvite: true,
        });
    }

    render() {
        const { inviteSent, hideInvite } = this.state;
		const { userStore } = this.props.stores;
        const { forceShow } = this.props;
        const { user } = userStore;
        const { settings = {} } = user;
        if (!forceShow && settings.hideInvite || hideInvite) {
            return '';
        }
        if (inviteSent) {
            return (
                <div class='col-12'>
                    <Text id='users.invite.thanks'>Takk for at du hjelper oss til  bli flere </Text>
                    <button type='button' class='btn btn-sm btn-link mb-2' onClick={this.inviteNew}>
                        <Text id='users.invite.more'>Invitere flere</Text>
                    </button>
                </div>
            );
        }
        return (
            <div class='col-12 pb-4 clearfix'>
                <h5><Text id='users.invite.title'>Inviter ny kennel til The Musher</Text></h5>
                <p>
                    <Text id='users.invite.text'>Jeg hper du vil hjelpe oss til  bli flere. Appen vil bli lansert veldig snart og det er ptide  invitere inn flere brukere.</Text>
                </p>
                <div>
                    <div class='form-row align-items-center'>
                        <div class='col-auto'>
                            <label class='sr-only' for='inlineFormInput'><Text id='users.invite.email'>E-post</Text></label>
                            <Localizer>
                                <input type='text' class='form-control form-control-sm mb-2' id='inlineFormInput'
                                    placeholder={<Text id='users.invite.email-placeholder'>E-post til ny kennel</Text>}
                                    onInput={linkState(this, 'friendEmail')}
                                />
                            </Localizer>
                        </div>
                        <div class='col-auto'>
                            <button type='button' class='btn btn-sm btn-primary mb-2' onClick={this.inviteKennel}>
                                <Text id='users.invite.send'>Send invitasjon</Text>
                            </button>
                        </div>
                    </div>
                </div>
                {!forceShow && <button type='button' class='btn btn-sm btn-link mb-2 float-left' onClick={this.hideInvite}>
                    <i class='fas fa-eye-slash' /> <Text id='users.invite.hide'>Skjul</Text>
                </button>}
            </div>
        );
    }
}

export default Invite;
